@use './styles/variables' as *;
@use './styles/mixins' as *;

// App  
.app-loading {
  display: flex;
  justify-content: center;
  align-items: center;
  min-height: 100vh;
  background: $background-beige;
  
  .loading-spinner {
    padding: $spacing-lg;
    font-size: $font-lg;
    color: $gray-600;
    text-align: center;
    
    &::after {
      content: '';
      display: inline-block;
      width: 20px;
      height: 20px;
      margin-left: $spacing-sm;
      border: 2px solid transparent;
      border-top: 2px solid $brand-brown;
      border-radius: 50%;
      animation: spin 1s linear infinite;
      vertical-align: middle;
    }
  }
}

@keyframes spin {
  0% { transform: rotate(0deg); }
  100% { transform: rotate(360deg); }
}

//   
@keyframes faviconBlink {
  0%, 100% { 
    opacity: 1; 
    transform: scale(1);
  }
  50% { 
    opacity: 0.3; 
    transform: scale(0.95);
  }
}

.favicon-blink-spinner {
  display: flex;
  justify-content: center;
  align-items: center;
}

//  
.app {
  border: none !important;
  border-bottom: none !important;
  border-top: none !important;
  border-left: none !important;
  border-right: none !important;
  outline: none !important;
  margin-bottom: 0 !important;
  padding-bottom: 0 !important;
  
  &::after,
  &::before {
    display: none !important;
    content: none !important;
    background: none !important;
    background-color: transparent !important;
    border: none !important;
    border-bottom: none !important;
    border-top: none !important;
    border-left: none !important;
    border-right: none !important;
    height: 0 !important;
    width: 0 !important;
    padding: 0 !important;
    margin: 0 !important;
    box-shadow: none !important;
    outline: none !important;
  }
  min-height: 100vh;
  padding: 0;
  margin: 0;
  width: 100%;
  overflow-x: hidden; //   
  position: relative;
  background: #FEF9EE !important; //    ( )
  
  //    
  &::before {
    content: '';
    position: absolute;
    top: 0;
    left: 0;
    right: 0;
    height: 0;
    margin: 0;
    padding: 0;
  }
}

//  
.main-container {
  width: 100%;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  background: transparent; // MainPage  
  overflow-y: auto;
  overflow-x: hidden; //   
  position: relative;
  animation: pageFadeIn 0.6s cubic-bezier(0.4, 0, 0.2, 1) forwards;
  border: none !important;
  border-bottom: none !important;
  border-top: none !important;
  border-left: none !important;
  border-right: none !important;
  outline: none !important;
  margin-bottom: 0 !important;
  padding-bottom: 0 !important;
  
  // :   ,  
  margin: 0;
  border-radius: 0;
  box-shadow: none;
  
  //     
  &::after,
  &::before {
    display: none !important;
    content: none !important;
    background: none !important;
    background-color: transparent !important;
    border: none !important;
    border-bottom: none !important;
    border-top: none !important;
    border-left: none !important;
    border-right: none !important;
    height: 0 !important;
    width: 0 !important;
    padding: 0 !important;
    margin: 0 !important;
    box-shadow: none !important;
    outline: none !important;
  }
  
  // :  ,   ,  
  @include respond-to(md) {
    max-width: 448px;
    margin: 0 auto;
    border-radius: $border-radius-xl;
    box-shadow: $shadow-lg;
    max-height: 900px;
    height: auto; //    (  )
    min-height: 100vh; //   
    background: transparent; //  .app 
  }
}

// health-data-viewer  main-container   
// (  :has()  )

// results-trend  main-container   
body:has([href*="results-trend"]) .main-container,
.main-container:has(.health-data-viewer) {
  max-width: 100% !important;
  margin: 0 !important;
  border-radius: 0 !important;
  box-shadow: none !important;
  
  @include respond-to(md) {
    max-width: 100% !important;
    margin: 0 !important;
    border-radius: 0 !important;
    box-shadow: none !important;
  }
}

//   
.bg-decoration {
  position: absolute;
  width: 256px; // w-64 (16rem = 256px)
  height: 256px; // h-64
  background-color: $brand-brown;
  border-radius: 50%;
  opacity: 0.1;
  filter: blur(48px); // blur-3xl
  z-index: -1;
  
  &--top {
    top: 25%; // top-1/4
    left: -96px; // -left-24 (6rem = 96px)
  }
  
  &--bottom {
    bottom: 25%; // bottom-1/4
    right: -96px; // -right-24
  }
}

// 
.header {
  flex-shrink: 0;
  z-index: 10;
  position: relative;
  height: 208px; // h-52 (13rem = 208px)
  
  &__image {
    position: absolute;
    inset: 0;
    width: 100%;
    height: 100%;
    object-fit: cover;
    object-position: center;
  }
  
  &__overlay {
    position: absolute;
    inset: 0;
    background: linear-gradient(to right, $background-beige, rgba(247, 232, 211, 0.8), transparent);
  }
  
  &__content {
    position: relative;
    z-index: 10;
    display: flex;
    flex-direction: column;
    justify-content: center;
    height: 100%;
    padding: $spacing-md $spacing-lg;
  }
  
  &__slogan {
    font-size: $font-sm;
    color: $gray-600;
    margin: 0 0 $spacing-xs 0;
  }
  
  &__logo {
    margin-top: $spacing-xs;
    
    &-title {
      font-size: $font-xl;
      font-weight: 700;
      color: $brand-brown;
      margin: 0;
    }
    
    &-subtitle {
      font-size: $font-xs;
      color: $gray-500;
      letter-spacing: 0.1em;
      margin: 0;
    }
  }
  
  &__main-title {
    font-size: $font-4xl;
    font-weight: 900;
    color: $gray-800;
    margin: $spacing-md 0 0 0;
  }
}

//  
.main-content {
  flex-grow: 1;
  display: flex;
  flex-direction: column;
  padding: $spacing-sm $spacing-md $spacing-md;
  text-align: center;
  z-index: 10;
}

//  
.title-section {
  flex-shrink: 0;
  margin-bottom: $spacing-md;
  
  &__title {
    font-size: $font-2xl;
    font-weight: 700;
    color: $gray-800;
    line-height: 1.25;
    margin: 0 0 $spacing-xs 0;
  }
  
  &__subtitle {
    font-size: $font-sm;
    color: $gray-500;
    margin: 0;
  }
}

// AI 
.ai-section {
  margin: $spacing-md 0;
  @include flex-center; //  
}

//    Card.scss 

//  
.health-card {
  background: rgba(255, 255, 255, 0.8);
  backdrop-filter: blur(16px);
  padding: $spacing-md;
  border-radius: $border-radius-xl;
  box-shadow: $shadow-md;
  border: 1px solid $gray-100;
  border-left: 4px solid $brand-brown;
  display: flex;
  align-items: center;
  gap: $spacing-md;
  transition: box-shadow 0.3s ease;
  text-align: left;
  
  &:hover {
    box-shadow: $shadow-lg;
  }
  
  &__icon {
    width: 48px; // w-12 (3rem = 48px)
    height: 48px; // h-12
    border-radius: $border-radius-lg;
    background-color: $brand-brown-light;
    color: $brand-brown;
    flex-shrink: 0;
    @include flex-center;
    
    .icon {
      width: 28px; // w-7 (1.75rem = 28px)
      height: 28px; // h-7
    }
  }
  
  &__content {
    flex: 1;
  }
  
  &__title {
    font-size: $font-sm;
    font-weight: 700;
    color: $gray-800;
    margin: 0 0 $spacing-xs 0;
  }
  
  &__description {
    font-size: $font-xs;
    color: $gray-600;
    margin: 0;
    
    strong {
      color: $brand-brown;
      font-weight: 600;
    }
  }
}

//  
.footer-section {
  flex-shrink: 0;
  margin-top: auto;
  
  &__message {
    font-size: 10px; //    
    color: $gray-600;
    font-weight: 500;
    margin: 0 0 $spacing-xs 0; //   
  }
  
  &__info {
    font-size: 9px; //    
    color: $gray-500;
    margin-bottom: $spacing-xs; //   
    
    p {
      margin: 0;
    }
  }
  
  &__spacing {
    height: 80px; //    
  }
  
  //    
  &--horizontal {
    text-align: center;
    
    .footer-section__message {
      text-align: center;
    }
    
    .footer-section__info {
      text-align: center;
    }
  }
  
  //     
  &--compact {
    margin-top: -$spacing-lg; //      
    
    .footer-section__message {
      margin-bottom: $spacing-xs; //     (sm  xs)
    }
    
    .footer-section__info {
      margin-bottom: $spacing-xs; //     (sm  xs)
    }
  }
}

//    
.horizontal-cards {
  &__title {
    font-size: $font-2xl;
    font-weight: 700;
    color: $gray-800;
    line-height: 1.2;
    text-align: center; //  
    margin: $spacing-md 0 $spacing-lg 0; //  
    
    //   
    .patient-name {
      font-size: $font-3xl; //   
      font-weight: 800;
      color: $gray-900; //    
    }
  }
  
  &__subtitle {
    font-size: $font-sm;
    color: $gray-600;
    line-height: 1.6;
    text-align: center; //  
    margin: 0 0 $spacing-xl 0; //   
    
    //   
    .hospital-name {
      color: $brand-brown;
      font-size: calc(#{$font-sm} + 1px); // 1px  
      font-weight: 600;
    }
  }
}

//    (  )
.horizontal-message-section {
  margin: $spacing-lg 0;
  padding: $spacing-md;
  text-align: center;
  
  &__text {
    font-size: $font-sm;
    color: $gray-700;
    font-weight: 500;
    line-height: 1.6;
    margin: 0;
    
    //    
    &::first-line {
      font-weight: 600;
      color: $gray-800;
    }
  }
}

//     ( ) - solid 
// Button       
//     (  )
html body:not([data-path*="login"]) button.floating-button:not(.questionnaire-container .floating-button),
html body:not([data-path*="login"]) .button.floating-button:not(.questionnaire-container .floating-button),
html body:not([data-path*="login"]) .floating-button.button:not(.questionnaire-container .floating-button),
html body .main-container button.floating-button:not(.questionnaire-container .floating-button),
html body .main-container .button.floating-button:not(.questionnaire-container .floating-button),
html body .main-container .floating-button.button:not(.questionnaire-container .floating-button),
button.floating-button:not(.questionnaire-container .floating-button),
.button.floating-button:not(.questionnaire-container .floating-button),
.floating-button.button:not(.questionnaire-container .floating-button) {
  position: fixed !important;
  bottom: 0 !important; //   
  left: 0 !important; //  
  right: 0 !important; //  (  )
  width: 100vw !important; //    ( )
  max-width: 100vw !important; //    
  min-width: 100vw !important; //   100vw
  min-height: $floating-button-height !important; //   ( )
  height: auto !important; //   
  max-height: none !important; //    
  background-color: $warning !important; //   ( )
  color: #FFFFFF !important; //  
  border: none !important; //  
  border-bottom: none !important; //    
  border-bottom-color: transparent !important; // border-bottom-color  transparent 
  border-top-color: transparent !important;
  border-left-color: transparent !important;
  border-right-color: transparent !important;
  outline: none !important; //  
  box-shadow: 0 -4px 12px rgba(237, 137, 54, 0.3) !important; //   ( )
  padding: $floating-button-padding-vertical $floating-button-padding-horizontal 0 !important; //  
  padding-bottom: 0 !important; //    
  overflow: hidden !important; //  
  clip-path: inset(0 0 0 0) !important; //     
  font-size: $floating-button-font-size !important; //   ( )
  font-weight: var(--font-weight-semibold) !important;
  font-family: var(--font-family-greeting) !important; //    (Noto Sans KR)
  border-radius: 0 !important; //  border-radius 
  border-top-left-radius: 12px !important; //    
  border-top-right-radius: 12px !important; //    
  border-bottom-left-radius: 0 !important; //     
  border-bottom-right-radius: 0 !important; //     
  // transition    (bottom, position, transform    )
  transition: background-color 0.3s ease, box-shadow 0.3s ease !important;
  z-index: 1000 !important;
  transform: none !important; // transform   (  )
  will-change: auto !important; //   
  box-sizing: border-box !important; //     
  margin: 0 !important; //   
  margin-bottom: 0 !important; //    
  
  //      -   
  &::after,
  &::before {
    display: none !important;
    content: none !important;
    background: none !important;
    background-color: transparent !important;
    border: none !important;
    border-top: none !important;
    border-bottom: none !important;
    border-left: none !important;
    border-right: none !important;
    height: 0 !important;
    width: 0 !important;
    padding: 0 !important;
    margin: 0 !important;
    box-shadow: none !important;
    outline: none !important;
  }
  
  // Button    
  &.button--primary,
  &.button--medium {
    border-radius: 0 !important;
    border-top-left-radius: 12px !important;
    border-top-right-radius: 12px !important;
    border-bottom-left-radius: 0 !important;
    border-bottom-right-radius: 0 !important;
    padding: 0.75rem 1rem 0 !important; //   
    padding-bottom: 0 !important; //    
    border: none !important;
    border-bottom: none !important;
  }
  
  // button__content span  -    
  .button__content {
    margin: 0 !important;
    padding: 0 !important;
    padding-bottom: 0 !important;
    border: none !important;
    border-top: none !important;
    border-bottom: none !important;
    border-left: none !important;
    border-right: none !important;
    background: transparent !important;
    background-color: transparent !important;
    display: block !important;
    line-height: 1 !important;
    height: auto !important;
    outline: none !important;
    box-shadow: none !important;
    
    &::after,
    &::before {
      display: none !important;
      content: none !important;
      background: none !important;
      background-color: transparent !important;
      border: none !important;
      border-top: none !important;
      border-bottom: none !important;
      border-left: none !important;
      border-right: none !important;
      height: 0 !important;
      width: 0 !important;
      padding: 0 !important;
      margin: 0 !important;
      box-shadow: none !important;
      outline: none !important;
    }
  }
  
  //    
  &::after {
    display: none !important;
    content: none !important;
    height: 0 !important;
    width: 0 !important;
    border: none !important;
    background: none !important;
    box-shadow: none !important;
  }
  
  //   border/outline  
  &:focus,
  &:focus-visible,
  &:active,
  &:hover,
  &:visited {
    border: none !important;
    border-bottom: none !important;
    border-top: none !important;
    border-left: none !important;
    border-right: none !important;
    outline: none !important;
    outline-offset: 0 !important;
    box-shadow: 0 -4px 12px rgba(124, 116, 106, 0.3) !important; //   
  }
  
  //     
  -webkit-appearance: none !important;
  -moz-appearance: none !important;
  appearance: none !important;
  
  //     
  @media (max-width: 767px) {
    left: 0 !important;
    right: 0 !important;
    width: 100vw !important;
    max-width: 100vw !important;
    transform: none !important;
    bottom: 0 !important; //   
    margin-bottom: 0 !important; //    
    padding-bottom: 0 !important; //    
  }
  
  &:hover {
    background-color: #4A3A32 !important; //    
    transform: none !important; // transform   (  )
    box-shadow: 0 6px 16px rgba(85, 67, 59, 0.4) !important; //   
  }
  
  &:active {
    transform: none !important; // transform  
  }
}

