//  
.modal-overlay {
  position: fixed;
  inset: 0;
  width: 100%;
  height: 100%;
  background: rgba(0, 0, 0, 0.5);
  z-index: 50;
  @include flex-center;
  padding: $spacing-md;
  transition: opacity 0.3s ease;
}

.modal-content {
  background: #fdf8e2; //   
  width: 100%;
  max-width: 448px; // max-w-md
  border-radius: $border-radius-2xl;
  box-shadow: $shadow-lg;
  padding: $spacing-xl;
  position: relative;
  transform: scale(1);
  transition: transform 0.3s ease;
  max-height: 90vh;
  overflow-y: auto;
}

.modal-close {
  position: absolute;
  top: $spacing-md;
  right: $spacing-md;
  background: none;
  border: none;
  color: $gray-500;
  cursor: pointer;
  padding: $spacing-xs;
  
  &:hover {
    color: $gray-800;
  }
  
  svg {
    width: 24px;
    height: 24px;
  }
}

.modal-title {
  font-size: $font-2xl;
  font-weight: 700;
  color: $brand-brown;
  margin: 0 0 $spacing-sm 0;
  text-align: center;
}

.modal-subtitle {
  font-size: $font-sm;
  color: $gray-600;
  margin: 0 0 $spacing-xl 0;
  text-align: center;
}

.form-fields {
  margin-bottom: $spacing-xl;
  text-align: left;
}

.gender-options {
  display: flex;
  gap: $spacing-md;
}

.gender-option {
  display: flex;
  align-items: center;
  cursor: pointer;
  
  input[type="radio"] {
    margin-right: $spacing-sm;
    accent-color: $brand-brown;
  }
}

//  
.modal-result {
  text-align: center;
}

.loader-container {
  @include flex-center;
  margin: $spacing-2xl 0;
}

.loader {
  border: 4px solid #f3f3f3;
  border-top: 4px solid $brand-brown;
  border-radius: 50%;
  width: 40px;
  height: 40px;
  animation: spin 1s linear infinite;
}

@keyframes spin {
  0% { transform: rotate(0deg); }
  100% { transform: rotate(360deg); }
}

.result-text {
  text-align: left;
  font-size: $font-sm;
  background: rgba(255, 255, 255, 0.8);
  padding: $spacing-md;
  border-radius: $border-radius-lg;
  min-height: 150px;
  white-space: pre-wrap;
  margin: $spacing-lg 0;
}

.result-disclaimer {
  font-size: $font-xs;
  color: $gray-500;
  text-align: center;
  margin: $spacing-md 0 0 0;
}

//   
.hospital-name {
  color: $brand-brown; //      
  font-size: 1.1em; //   +1 ( 10% )
  font-weight: 600; //  
}

//  
@include respond-to(md) {
  .main-container {
    margin: 0;
    border-radius: 0;
  }
  
  .header {
    &__content {
      padding: $spacing-lg $spacing-2xl;
    }
  }
  
  .main-content {
    padding: $spacing-md $spacing-xl $spacing-xl;
  }
}
