/**
 * HealthTrendsHeader 
 *         
 */
@use '../../../styles/variables' as *;
@use '../../../styles/mixins' as *;

.health-trends-header {
  //        
  position: fixed !important; //    (  )
  top: 0 !important; //  
  left: 0 !important; //   (  )
  right: 0 !important; //  (  )
  width: 100vw !important; //    (  )
  max-width: 100vw !important; //    
  min-width: 100vw !important; //   100vw
  box-sizing: border-box !important;
  z-index: 100 !important; //    
  padding: 0 !important; //    
  margin: 0 !important; //   
  margin-left: calc(-50vw + 50%) !important; //     
  margin-right: calc(-50vw + 50%) !important; //     
  transform: none !important; // transform  (left/right  )
  
  //      (  )
  background: #FEF9EE !important; //  
  border-radius: 0 0 24px 24px !important; //   (   , 12px -> 24px)
  box-shadow: 0 2px 8px rgba(0, 0, 0, 0.08) !important; //    
  min-height: 90px !important; //    (default )
  height: auto !important; //   (    -   )
  display: flex !important;
  
  // large      (description    ,    )
  &--large {
    min-height: 120px !important; // :   
    // ( + )    
  }
  
  //   CSS   (  )
  --header-height: 90px; //  (default )
  
  &--large {
    --header-height: 120px; // large 
  }
  align-items: center !important; //   
  padding-left: $spacing-md !important; //     
  padding-right: $spacing-md !important; //     
  padding-top: $spacing-md !important; //   (    )
  padding-bottom: $spacing-md !important; //   (    )
  position: relative !important; //   
  box-sizing: border-box !important; //    

  //   (  )
  &__header-greeting-section {
    width: 100% !important; //    
    min-height: 100% !important; //   
    height: auto !important; //     
    margin: 0 !important;
    padding: 0 !important; //   ( )
    position: relative !important;
    display: flex !important;
    align-items: center !important; //   

    //   () -   
    .back-button {
      position: absolute !important; //  
      left: $spacing-md !important; //   
      top: 50% !important; //  
      transform: translateY(-50%) !important; //   
      z-index: 1 !important; //   
    }
  }

  //   (     )
  &__center {
    width: 100% !important; //    
    display: flex;
    flex-direction: column;
    align-items: center; // X   (   )
    justify-content: center; // Y  
    gap: $spacing-xs; //  /  
    position: relative !important; // relative    
    z-index: 0 !important; //  
    padding: $spacing-md $spacing-md !important; //   (    )
    box-sizing: border-box !important;
    flex: 1 !important; //   
    min-height: fit-content !important; //    
  }

  //  -    
  &__title {
    text-align: center; //  
    font-family: $header-title-font-family !important;
    font-weight: $header-title-font-weight !important; // 500
    font-size: $header-title-font-size !important; // 18px
    line-height: $header-title-line-height !important; // 26px (    144%)
    color: $header-title-color !important; // #000000
    margin: 0 !important;
  }

  //   ( ,      ) -    
  &__description {
    text-align: center; //  
    font-family: $header-description-font-family !important;
    font-weight: $header-description-font-weight !important; // 400 (Regular)
    font-size: $header-description-font-size !important; // 12px
    line-height: $header-description-line-height !important; // 16px
    letter-spacing: $header-description-letter-spacing !important; // 0%
    color: $gray-700 !important; // #4a5568
    margin: 8px 0 0 0 !important; //    ( )
    margin-top: 12px !important; //   
    padding: 0 $spacing-lg !important; //    (   )
    padding-left: 60px !important; //    (   )
    padding-right: 60px !important; //    ()
    white-space: pre-line !important; //  
  }

  //    ( ,  )
  &__update {
    display: flex;
    align-items: center;
    justify-content: center; //  
    gap: 6px;
    font-size: $font-xs; // 12px
    color: $gray-550;
    font-family: var(--font-family-greeting, 'Noto Sans KR', sans-serif);
    
    &-icon {
      font-size: $font-xs;
      color: $gray-550;
    }
    
    &-text {
      font-size: $font-xs;
      color: $gray-550;
    }
    
  }
  
  //   (   )
  &__refresh-icon {
    width: 18px;
    height: 18px;
    border: none;
    background: transparent;
    cursor: pointer;
    padding: 0;
    display: flex;
    align-items: center;
    justify-content: center;
    position: relative;
    flex-shrink: 0;
    transition: all 0.2s ease;
    
    &:hover {
      transform: scale(1.1) rotate(90deg);
    }
    
    &:active {
      transform: scale(0.95) rotate(90deg);
    }
    
    // SVG  
    &-svg {
      width: 14px;
      height: 14px;
      transition: transform 0.3s ease;
    }
    
    &:hover &-svg {
      transform: rotate(180deg);
    }
  }

  //  
  @media (max-width: 767px) {
    padding: $spacing-sm $spacing-sm !important;
    min-height: 52px; //   (default )
    --header-height: 52px; //  
    gap: $spacing-sm;
    
    // large     
    &.health-trends-header--large {
      min-height: 80px !important; // :  
      --header-height: 80px; //  large 
    }
    
    &__back-button {
      width: 36px;
      height: 36px;
      
      svg {
        width: 18px;
        height: 18px;
      }
    }
    
    &__title {
      font-size: $font-base; // 16px
    }

    &__description {
      font-size: 13px !important; //   
      padding: 0 $spacing-sm !important; //   
    }
    
    &__update {
      font-size: 11px;
      
      &-icon {
        font-size: 11px;
      }
      
      &-text {
        font-size: 11px;
      }
    }
  }
}

