            }
          }
        }
      }
    }
    
    //     
    .group-items {
      .checkup-item {
        padding: $spacing-sm;
        
        .item-header {
          flex-direction: column;
          align-items: flex-start;
          gap: $spacing-xs;
        }
        
        .item-value {
          font-size: $font-base;
        }
      }
    }
  }
}

.favicon-blink-spinner {
  display: flex;
  justify-content: center;
  align-items: center;
  margin-bottom: $spacing-md;
}

@keyframes slideDown {
  from {
    opacity: 0;
    max-height: 0;
  }
  to {
    opacity: 1;
    max-height: 500px;
  }
}

@keyframes fadeIn {
  from {
    opacity: 0;
  }
  to {
    opacity: 1;
  }
}

@keyframes fadeInToFull {
  from {
    opacity: 0.3; //  
  }
  to {
    opacity: 1; // 
  }
}

//   
.dot-indicators {
  display: flex;
  justify-content: center;
  gap: 4px; // $spacing-xs  
  margin-top: $spacing-xs; // $spacing-sm  $spacing-xs 
  
  .dot {
    width: 6px; //  
    height: 6px; //   (width height )
    min-width: 6px; //   
    min-height: 6px; //   
    border-radius: 50%; //  
    border: none;
    padding: 0; //  
    background: $gray-300;
    cursor: pointer;
    transition: all 0.2s ease;
    flex-shrink: 0; //  
    
    &:hover {
      background: $gray-400;
      transform: scale(1.1); // 1.2  1.1 
    }
    
    &.active {
      background: $brand-brown;
      transform: scale(1.2); // 1.3  1.2 
    }
  }
}
