html, body {
  touch-action: pan-x pan-y;
  -webkit-text-size-adjust: none;
  overflow-x: hidden;
  width: 100%;
  position: relative;
  //    (  )
  // user-select: none;
  // -webkit-user-select: none;
  // -moz-user-select: none;
  // -ms-user-select: none;
}

//   
img {
  -webkit-user-drag: none;
  -khtml-user-drag: none;
  -moz-user-drag: none;
  -o-user-drag: none;
  user-drag: none;
  pointer-events: auto;
}
