@import '../../../styles/variables';

.chat-interface {
  width: 100%;
  min-height: 100vh;
  background: $background-cream; // ConcernSelection  
  box-sizing: border-box;
  position: relative; //      

  //   -      (ContentLayoutWithHeader  )
  &__body {
    position: fixed;
    top: var(--header-height, 90px); //   (large    )
    left: 0;
    right: 0;
    bottom: 60px; //       (progress body  )
    overflow-y: auto; //    ( + )
    overflow-x: hidden;
    -webkit-overflow-scrolling: touch;
    background: $background-cream;
    padding-bottom: 0;
    will-change: scroll-position;
    overscroll-behavior-y: contain;
    display: flex;
    flex-direction: column;
    z-index: 1; //  (z-index: 1000)      
  }

  &__messages {
    flex: 1;
    padding: $spacing-md;
    display: flex;
    flex-direction: column;
    gap: $spacing-md;
    min-height: 0; // flex overflow  
  }

  &__options {
    display: flex;
    flex-direction: column; //    (  + )
    gap: $spacing-xs; //     (   )
    padding: $spacing-md $spacing-md $spacing-md 0; //     
    margin-left: $spacing-xl; //    (  ,   )
    // margin-top : __messages gap   (  )
    background: transparent; //  
    border-top: none; //  
    flex-shrink: 0; //    
    position: relative; //    
    
    //    ( )
    opacity: 0;
    transform: translateX(50px);
    transition: opacity 0.6s ease-out, transform 0.6s ease-out;
    
    &.is-visible {
      opacity: 1;
      transform: translateX(0);
    }
  }

  //    ( )
  &__options-slider {
    display: flex;
    flex-direction: row; //  
    gap: $spacing-md;
    overflow-x: auto; //   (/  )
    overflow-y: hidden;
    -webkit-overflow-scrolling: touch;
    scroll-snap-type: x mandatory; //  
    margin-bottom: 0; //   
    padding-bottom: 0; //   
    
    //   (  )
    scrollbar-width: none; // Firefox
    -ms-overflow-style: none; // IE, Edge
    
    &::-webkit-scrollbar {
      display: none; // Chrome, Safari, Opera
    }
    
    //  0 
    &--empty {
      display: none;
    }
    
    //  1 :  
    &--single {
      justify-content: flex-end;
    }
    
    //    :   ()
    &--multiple {
      justify-content: flex-start;
    }
  }

  //  
  &__dots {
    display: flex;
    justify-content: center;
    gap: $spacing-xs;
    padding: $spacing-sm 0;
    position: absolute;
    bottom: -$spacing-md;
    left: 50%;
    transform: translateX(-50%);
    z-index: 10;
  }

  &__dot {
    width: 6px;
    height: 6px;
    border-radius: 50%;
    background: $gray-400;
    cursor: pointer;
    transition: all 0.2s ease;
    border: none;
    padding: 0;
    flex-shrink: 0;

    &--active {
      background: $brand-brown;
      width: 20px;
      border-radius: 3px;
    }
  }

  &__progress {
    position: fixed; // body   fixed 
    bottom: 0;
    left: 0;
    right: 0;
    padding: $spacing-sm $spacing-md;
    background: $gray-100;
    border-top: 1px solid $gray-300;
    flex-shrink: 0; //     
    z-index: 10;
    display: flex;
    flex-direction: column;
    gap: $spacing-sm;
    height: 60px; //  
  }

  &__progress-info {
    text-align: center;
    font-size: $font-sm;
    color: $gray-600;
  }

  &__progress-actions {
    display: flex;
    justify-content: flex-end; //  
    align-items: center;
    gap: $spacing-xs;
    padding: 0; //  
    margin-left: 0; //     ( )
    margin-right: 0; //   
    opacity: 0;
    transform: translateY(10px);
    transition: opacity 0.4s ease-out, transform 0.4s ease-out;
    
    &--visible {
      opacity: 1;
      transform: translateY(0);
    }
  }

  &__progress-actions-right {
    display: flex;
    gap: $spacing-xs;
    margin-left: auto; //  
  }

  &__button {
    padding: $spacing-sm $spacing-md;
    border-radius: $border-radius-md;
    font-size: $font-base;
    font-weight: $font-weight-semibold;
    cursor: pointer;
    transition: all 0.2s ease;
    border: none;
    font-family: $font-family-base;

    &--small {
      padding: $spacing-xs $spacing-sm;
      font-size: $font-sm;
      flex: 0 0 auto; //  
    }

    &--primary {
      background: $brand-brown-dark;
      color: white;

      &:hover {
        background: $brand-brown-hover-dark;
        transform: translateY(-1px);
        box-shadow: $shadow-md;
      }

      &:active {
        transform: translateY(0);
      }
    }

    &--secondary {
      background: $gray-200;
      color: $gray-700;

      &:hover {
        background: $gray-300;
        transform: translateY(-1px);
        box-shadow: $shadow-sm;
      }

      &:active {
        transform: translateY(0);
      }
    }

    &--select-all {
      background: $brand-brown-light-bg;
      color: $brand-brown-dark;
      border: 1px solid $brand-brown-dark;

      &:hover {
        background: $brand-brown-light;
        transform: translateY(-1px);
        box-shadow: $shadow-sm;
      }

      &:active {
        transform: translateY(0);
      }
    }

    &--select-all {
      background: $brand-brown-light-bg;
      color: $brand-brown-dark;
      border: 1px solid $brand-brown-dark;

      &:hover {
        background: $brand-brown-light;
        transform: translateY(-1px);
        box-shadow: $shadow-sm;
      }

      &:active {
        transform: translateY(0);
      }
    }
  }

  //    ( )
  &__thinking {
    display: flex;
    align-items: center;
    width: 100%;
    padding: $spacing-xs 0;
    animation: fadeIn 0.3s ease-out;
    gap: $spacing-xs;
    
    // :   (  )
    &--left {
      justify-content: flex-start;
    }
    
    //   (    -   )
    &--right {
      justify-content: flex-end;
    }
    
    //   
    &.fade-out {
      animation: fadeOut 0.3s ease-out forwards;
    }
  }
  
  //  
  &__thinking-text {
    font-size: $font-xs; //   
    color: $brand-brown-darker; //  
    font-style: normal; //  
    white-space: nowrap;
    transition: opacity 0.8s ease-in-out, transform 0.8s ease-in-out; //   
    opacity: 1;
    transform: translateY(0);
    display: inline-block;
    
    //      
    animation: fadeInOut 1.5s ease-in-out infinite;
  }

  &__thinking-spinner {
    width: 32px;
    height: 32px;
    display: flex;
    align-items: center;
    justify-content: center;
  }
  
  //     
  &__thinking--left &__thinking-spinner {
    margin-left: $spacing-md;
  }
  
  //     
  &__thinking--right &__thinking-spinner {
    margin-right: $spacing-md;
  }

  &__thinking-welno-icon {
    width: 24px;
    height: 24px;
    object-fit: contain;
    animation: faviconBlink 2s cubic-bezier(0.4, 0, 0.2, 1) infinite;
  }
}

@keyframes faviconBlink {
  0%, 100% {
    opacity: 1;
    transform: scale(1) rotate(0deg);
  }
  25% {
    opacity: 0.7;
    transform: scale(0.98) rotate(2deg);
  }
  50% {
    opacity: 0.4;
    transform: scale(0.96) rotate(0deg);
  }
  75% {
    opacity: 0.7;
    transform: scale(0.98) rotate(-2deg);
  }
}

@keyframes spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

@keyframes slideInFromRight {
  from {
    opacity: 0;
    transform: translateX(30px);
  }
  to {
    opacity: 1;
    transform: translateX(0);
  }
}

//  
.chat-message {
  display: flex;
  width: 100%;
  animation: fadeInMessage 0.6s ease-out;
  animation-fill-mode: both;

  &--bot {
    justify-content: flex-start;

    //  ( )   
    .chat-message__content {
      font-size: $font-base; // 16px  (14px -> 16px)
      line-height: 1.6; //   line-height  
    }
  }

  &--user {
    justify-content: flex-end;
  }

  &__bubble {
    max-width: 55%; // 65% 55%  
    padding: $spacing-xs $spacing-sm; //   
    border-radius: $border-radius-lg;
    word-wrap: break-word;
    font-family: $font-family-base;

    &--bot {
      background: $brand-brown-light-bg; //    (#F8EDDA)
      color: $gray-800;
      border-bottom-left-radius: $border-radius-sm;
    }

    &--user {
      background: $brand-brown;
      color: $white;
      border-bottom-right-radius: $border-radius-sm;
    }
  }

  &__content {
    font-size: $font-sm; // 16px 14px 
    line-height: 1.5; // 1.6 1.5 
    white-space: pre-line; //  
  }

  //    ( )
  &__highlight-period {
    color: $brand-brown-darker; //  (#55433B)
    font-weight: $font-weight-bold; // 
    font-weight: $font-weight-normal; // 400
    word-break: keep-all; //   
  }

  &__timestamp {
    font-size: 10px; // $font-xs (12px) 10px 
    color: $gray-500;
    margin-top: $spacing-xs;
    text-align: right;
  }
}

//  
.chat-option-button {
  display: flex;
  align-items: center;
  gap: $spacing-sm;
  min-width: 160px; // 200px 160px 
  max-width: 200px; // 250px 200px 
  width: 160px; // 200px 160px 
  padding: $spacing-sm $spacing-md; //  
  background: $white;
  border: 1px solid $gray-300;
  border-radius: $border-radius-lg;
  text-align: left;
  cursor: pointer;
  transition: all 0.2s ease;
  font-family: $font-family-base;
  flex-shrink: 0; //  
  scroll-snap-align: start; //  
  
  //      ( )
  opacity: 0;
  transform: translateX(30px);
  animation: slideInFromRight 0.5s ease-out forwards;

  &:hover {
    background: $gray-50;
    border-color: $brand-brown;
    transform: translateY(-1px);
    box-shadow: $shadow-md;
  }

  &--selected {
    background: $brand-brown-light;
    border-color: $brand-brown;
    box-shadow: $shadow-sm;
  }

  &__icon {
    font-size: $font-lg;
  }

  &__content {
    flex: 1;
    display: flex;
    flex-direction: column;
    gap: $spacing-xs;
  }

  &__label {
    font-size: $font-base;
    font-weight: $font-weight-medium;
    color: $gray-800;
  }

  &__description {
    font-size: $font-sm;
    color: $gray-600;
  }
}

//  
.medication-card {
  display: flex;
  flex-direction: column;
  min-width: 240px; // 180px 240px 
  max-width: 280px; // 220px 280px 
  width: 240px; // 180px 240px 
  min-height: 120px; // 140px 120px 
  padding: $spacing-xs $spacing-sm; //   
  background: $white;
  border: 1px solid $gray-300;
  border-radius: $border-radius-lg;
  text-align: left;
  cursor: pointer;
  transition: all 0.2s ease;
  font-family: $font-family-base;
  flex-shrink: 0; //  
  scroll-snap-align: start; //  
  
  //      ( )
  opacity: 0;
  transform: translateX(30px);
  animation: slideInFromRight 0.5s ease-out forwards;

  &:hover {
    border-color: $brand-brown;
    transform: translateY(-2px);
    box-shadow: $shadow-lg;
  }

  &--selected {
    background: $brand-brown-light;
    border-color: $brand-brown;
    box-shadow: $shadow-md;
  }

  &__header {
    display: flex;
    justify-content: space-between;
    align-items: center; // flex-start center     
    margin-bottom: $spacing-sm;
    gap: $spacing-xs; //    
  }

  &__checkbox-wrapper {
    flex-shrink: 0;
    display: flex;
    align-items: center;
    margin-top: 0; //   margin-top 
  }

  &__checkbox {
    width: 14px;
    height: 14px;
    cursor: pointer;
    appearance: none;
    border: 2px solid $brand-brown-dark;
    background: $white;
    border-radius: $border-radius-sm;
    position: relative;
    transition: all 0.2s ease;
    flex-shrink: 0;
    
    &:checked {
      background: $brand-brown-dark;
      border-color: $brand-brown-dark;
      
      &::after {
        content: '';
        position: absolute;
        top: 50%;
        left: 50%;
        transform: translate(-50%, -50%);
        color: $white;
        font-size: 10px;
        font-weight: $font-weight-bold;
      }
    }
    
    &:hover {
      border-color: $brand-brown-darker;
      transform: scale(1.05);
    }
  }

  &__effect {
    font-size: $font-base; // 16px  (12px -> 16px)
    font-weight: $font-weight-semibold;
    color: $gray-800;
    margin: 0;
    flex: 1; //   
    line-height: 1.4; //   line-height 
  }
  
  &__prescription-count-badge {
    padding: 2px $spacing-xs;
    border-radius: $border-radius-sm;
    font-size: 0.625rem; // $font-xs (12px) 10px -2px
    font-weight: $font-weight-medium;
    background: $brand-brown-light-bg; //   
    color: $brand-brown-darker; //   
    white-space: nowrap;
    flex-shrink: 0;
  }

  &__badges {
    display: flex;
    gap: $spacing-xs;
    flex-wrap: wrap;
  }

  &__badge {
    padding: 2px $spacing-xs;
    border-radius: $border-radius-sm;
    font-size: $font-xs;
    font-weight: $font-weight-medium;

    &--long-term {
      background: $badge-warning-bg;
      color: $badge-warning-text;
    }

    &--recent {
      background: $badge-normal-bg;
      color: $badge-normal-text;
    }
  }

  &__body {
    display: flex;
    flex-direction: column;
    gap: $spacing-xs;
  }

  &__info {
    display: flex;
    flex-direction: column;
    gap: 2px;
  }

  &__period {
    font-size: $font-sm; // 14px  (10px -> 14px)
    color: $gray-600;
    font-weight: $font-weight-medium;
  }

  &__duration {
    font-size: $font-sm; // 14px  (10px -> 14px)
    color: $gray-700;
    font-weight: $font-weight-medium;
  }

  &__details {
    display: flex;
    gap: $spacing-sm;
    margin-top: $spacing-xs;
  }

  &__count {
    display: none; //   
  }

  &__restart {
    font-size: $font-sm;
    color: $gray-500;
  }
}

