  //   
  &__details {
    margin-top: $spacing-sm;
    padding-top: $spacing-sm;
    border-top: 1px solid $gray-200;
    display: flex;
    flex-direction: column;
    gap: $spacing-sm;
    width: 100%;
    overflow: visible;
    position: relative;
    z-index: 1;
  }

  &__group-slider {
    display: flex;
    gap: $spacing-xs;
    overflow-x: auto;
    padding-bottom: $spacing-xs;
    scrollbar-width: none;
    -ms-overflow-style: none;
    
    &::-webkit-scrollbar {
      display: none;
    }
  }

  &__group-tab {
    padding: $spacing-xs $spacing-sm;
    border: 1px solid $gray-300;
    border-radius: $border-radius-sm;
    background: $white;
    color: $gray-700;
    font-size: $font-xs;
    cursor: pointer;
    white-space: nowrap;
    transition: all 0.2s ease;
    
    &:hover {
      border-color: $brand-brown;
      color: $brand-brown;
    }
    
    &.active {
      background: $brand-brown;
      border-color: $brand-brown;
      color: $white;
    }
  }

  &__status-badges {
    display: flex;
    gap: $spacing-xs;
    flex-wrap: wrap;
  }

  &__status-badge {
    padding: 2px $spacing-xs;
    border-radius: $border-radius-sm;
    font-size: $font-xs;
    font-weight: $font-weight-medium;
    
    &.status-normal {
      background: $badge-normal-bg;
      color: $badge-normal-text;
    }
    
    &.status-warning {
      background: $badge-warning-bg;
      color: $badge-warning-text;
    }
    
    &.status-abnormal {
      background: $badge-abnormal-bg;
      color: $badge-abnormal-text;
    }
  }

  &__items-slider {
    display: flex;
    flex-direction: row;
    gap: $spacing-sm;
    overflow-x: auto;
    overflow-y: hidden;
    padding: $spacing-sm $spacing-xs;
    scroll-snap-type: x mandatory;
    scrollbar-width: none;
    -ms-overflow-style: none;
    width: 100%;
    max-width: 100%;
    
    &::-webkit-scrollbar {
      display: none;
    }
    
    //  0 
    &--empty {
      display: flex;
      align-items: center;
      justify-content: center;
      min-height: 60px;
    }
    
    //  1 :  
    &--single {
      justify-content: flex-end;
    }
    
    //    :   ()
    &--multiple {
      justify-content: flex-start;
    }
  }
  
  &__items-empty {
    font-size: $font-sm;
    color: $gray-500;
    text-align: center;
    padding: $spacing-md;
  }

  &__item {
    position: relative;
    min-width: 200px;
    max-width: 200px;
    width: 200px;
    padding: $spacing-sm;
    background: $gray-50;
    border-radius: $border-radius-md;
    border: 1px solid $gray-200;
    cursor: pointer;
    transition: all 0.2s ease;
    flex-shrink: 0;
    flex-grow: 0;
    scroll-snap-align: start;
    user-select: none;
    
    &:hover {
      border-color: $brand-brown;
      box-shadow: $shadow-sm;
    }
    
    &--selected {
      background: $brand-brown-light;
      border-color: $brand-brown;
      box-shadow: $shadow-md;
    }
  }

  &__item-checkbox-wrapper {
    position: absolute;
    top: $spacing-xs;
    left: $spacing-xs;
    z-index: 10;
  }

  &__item-checkbox {
    width: 14px;
    height: 14px;
    cursor: pointer;
    appearance: none;
    border: 2px solid $brand-brown-dark;
    background: $white;
    border-radius: $border-radius-sm;
    position: relative;
    transition: all 0.2s ease;
    
    &:checked {
      background: $brand-brown-dark;
      border-color: transparent; //     
      
      &::after {
        content: '';
        position: absolute;
        top: 50%;
        left: 50%;
        transform: translate(-50%, -50%);
        color: $white;
        font-size: 10px;
        font-weight: $font-weight-bold;
      }
    }
    
    &:hover {
      border-color: $brand-brown-darker;
      transform: scale(1.05);
    }
  }

  &__item-badge {
    position: absolute;
    top: $spacing-xs;
    right: $spacing-xs;
    width: 24px;
    height: 24px;
    border-radius: 50%;
    display: flex;
    align-items: center;
    justify-content: center;
    font-size: $font-xs;
    font-weight: $font-weight-bold;
    color: $white;
    z-index: 5;
    
    &.status-normal {
      background: $badge-normal-bg;
    }
    
    &.status-warning {
      background: $badge-warning-bg;
    }
    
    &.status-abnormal {
      background: $badge-abnormal-bg;
    }
  }

  &__item-name {
    font-size: $font-sm; // 14px  (10px -> 14px,   period )
    font-weight: $font-weight-semibold;
    color: $gray-800;
    margin-bottom: $spacing-xs;
    padding-left: 24px; //    (14px + 10px)
    padding-right: 30px; //   
  }

  &__item-value {
    font-size: $font-base; // $font-lg -> $font-base 
    font-weight: $font-weight-bold;
    color: $gray-900;
    margin-bottom: $spacing-xs;
  }

  &__item-unit {
    font-size: $font-sm; // 14px  (10px -> 14px,   duration )
    font-weight: $font-weight-normal;
    color: $gray-600;
    margin-left: 2px;
  }

  &__body-measurements {
    display: flex;
    flex-direction: column;
    gap: $spacing-xs;
  }

  &__measurement {
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding: $spacing-xs 0;
    border-bottom: 1px solid $gray-200;
    
    &:last-child {
      border-bottom: none;
    }
  }

  &__measurement-label {
    font-size: $font-sm; //   period 
    color: $gray-600;
    font-weight: $font-weight-medium;
  }

  &__measurement-value {
    font-size: $font-base; //   effect 
    font-weight: $font-weight-semibold;
    color: $gray-800;
  }

  &__measurement-unit {
    font-size: $font-sm; // 14px  (  duration )
    color: $gray-600;
    margin-left: 2px;
  }

  &__item-references {
    display: flex;
    flex-direction: column;
    gap: 2px;
    margin-top: $spacing-xs;
    padding-top: $spacing-xs;
    border-top: 1px solid $gray-200;
  }

  &__reference {
    display: flex;
    justify-content: space-between;
    font-size: $font-sm; // 14px  (  period )
    color: $gray-600;
  }

  &__reference-label {
    font-weight: $font-weight-medium;
  }

  &__reference-range {
    color: $gray-500;
  }
}

// 
@keyframes fadeIn {
  from {
    opacity: 0;
  }
  to {
    opacity: 1;
  }
}

@keyframes fadeOut {
  from {
    opacity: 1;
  }
  to {
    opacity: 0;
  }
}

@keyframes fadeInMessage {
  from {
    opacity: 0;
    transform: translateY(5px);
  }
  to {
    opacity: 1;
    transform: translateY(0);
  }
}

@keyframes fadeInOut {
  0% {
    opacity: 0.3;
    transform: translateY(3px);
  }
  50% {
    opacity: 1;
    transform: translateY(0);
  }
  100% {
    opacity: 0.3;
    transform: translateY(-3px);
  }
}

//    
.chat-message {
  &:nth-child(1) { animation-delay: 0s; }
  &:nth-child(2) { animation-delay: 0.2s; }
  &:nth-child(3) { animation-delay: 0.4s; }
  &:nth-child(4) { animation-delay: 0.6s; }
  &:nth-child(5) { animation-delay: 0.8s; }
  &:nth-child(n+6) { animation-delay: 1s; }
}

//  
@media (max-width: $breakpoint-sm) {
  .chat-message__bubble {
    max-width: 85%;
  }

  .medication-card,
  .checkup-card {
    padding: $spacing-sm;
  }
}

