/**
 * DateSelector 
 *    
 */
@use '../../../styles/variables' as *;

.date-selector {
  width: 100%;
  max-width: 100%;
  overflow: hidden; //    
  
  //  
  &__card {
    background: $white;
    border: 1px solid $border-beige;
    border-radius: $border-radius-lg;
    padding: $spacing-sm $spacing-xs; //   (  )
    box-shadow: $shadow-sm;
    width: 100%;
    max-width: 100%;
    box-sizing: border-box;
  }
  
  //   
  &__header {
    display: flex;
    align-items: center;
    justify-content: space-between;
    margin-bottom: $spacing-sm; //  
  }
  
  //   
  &__nav-button {
    width: 40px;
    height: 40px;
    border-radius: 50%;
    background: $brand-brown-light-bg;
    border: none;
    display: flex;
    align-items: center;
    justify-content: center;
    cursor: pointer;
    transition: all 0.2s ease;
    color: $black;
    
    &:hover {
      background: $brand-brown-light-bg-hover;
      transform: scale(1.05);
    }
    
    &:active {
      transform: scale(0.95);
    }
    
    svg {
      width: 20px;
      height: 20px;
    }
  }
  
  //   
  &__month {
    font-size: $font-lg; //    ( : 18px)
    font-weight: $font-weight-bold;
    color: $black;
    font-family: var(--font-family-greeting, 'Noto Sans KR', sans-serif);
    line-height: 1.2;
  }
  
  //  
  &__weekdays {
    display: grid;
    grid-template-columns: repeat(7, 1fr);
    gap: 2px; //  
    margin-bottom: $spacing-xs; //  
    width: 100%;
  }
  
  &__weekday {
    text-align: center;
    font-size: $font-base; //    ( : 16px)
    font-weight: $font-weight-medium;
    color: $black;
    padding: 4px 0; //  
    font-family: var(--font-family-greeting, 'Noto Sans KR', sans-serif);
    
    //  
    &--sunday {
      color: #ef4444; // 
    }
  }
  
  //  
  &__grid {
    display: grid;
    grid-template-columns: repeat(7, 1fr);
    gap: 2px; //   (   )
    width: 100%;
    box-sizing: border-box;
  }
  
  //  
  &__day {
    aspect-ratio: 1;
    //       (    )
    width: 100%;
    max-width: 100%;
    max-height: 40px; //    (  )
    min-height: 0; // aspect-ratio  
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    cursor: pointer;
    border-radius: $border-radius-md;
    transition: all 0.2s ease;
    position: relative;
    padding: 2px 1px; //   ( )
    box-sizing: border-box;
    
    //   ( )
    &-number {
      font-size: $font-base; //    ( : 16px)
      font-weight: $font-weight-normal;
      color: $black;
      font-family: var(--font-family-greeting, 'Noto Sans KR', sans-serif);
      line-height: 1.1; //  
      white-space: nowrap; //   
    }
    
    //  
    &-preference {
      font-size: 8px !important; //   +2
      font-weight: $font-weight-normal;
      color: $black;
      margin-top: 0; //  
      line-height: 1;
      font-family: var(--font-family-greeting, 'Noto Sans KR', sans-serif);
      white-space: nowrap; //   
    }
    
    //   ( )
    &--today {
      background: $brand-brown-light-bg;
      
      .date-selector__day-number {
        color: $black;
      }
    }
    
    //   ( )
    &--selected {
      background: $brand-brown-dark;
      
      .date-selector__day-number {
        color: $white;
        font-weight: $font-weight-semibold;
      }
      
      .date-selector__preference {
        color: $white !important;
        font-size: 8px !important; //   +2
      }
    }
    
    //    (/ )
    &--other-month {
      .date-selector__day-number {
        color: $gray-666;
      }
    }
    
    //    (/)
    &--disabled {
      cursor: not-allowed;
      opacity: 0.5;
      
      .date-selector__day-number {
        color: $gray-666;
      }
    }
    
    //  
    &--empty {
      cursor: default;
      pointer-events: none;
    }
    
    //   (  )
    &--sunday {
      .date-selector__day-number {
        color: #ef4444; // 
      }
    }
    
    //   (  )
    &:not(&--disabled):not(&--empty):not(&--selected):hover {
      background: rgba($brand-brown-light-bg, 0.5);
    }
  }
  
  //  
  @media (max-width: 767px) {
    &__card {
      padding: $spacing-xs; //  
    }
    
    &__header {
      margin-bottom: $spacing-xs; //  
    }
    
    &__nav-button {
      width: 36px;
      height: 36px;
      flex-shrink: 0; //   
      
      svg {
        width: 18px;
        height: 18px;
      }
    }
    
    &__month {
      font-size: $font-base; //    ( )
      flex: 1;
      text-align: center;
    }
    
    &__weekdays {
      gap: 2px;
      margin-bottom: $spacing-xs; //  
    }
    
    &__weekday {
      font-size: $font-sm; //   
      padding: 2px 0; //  
    }
    
    &__grid {
      gap: 2px;
    }
    
    &__day {
      max-height: 38px; //    (  )
      padding: 1px; //  
      
      &-number {
        font-size: $font-sm; //    ( : 14px)
      }
      
      &-preference {
        font-size: 7px !important; //  +2
        margin-top: 0;
      }
    }
  }
}

