/**
 * HealthTrendsToggle 
 * 2   : ( ), ()
 *    
 */
@use '../../../styles/variables' as *;
@use '../../../styles/mixins' as *;

.health-trends-toggle {
  width: 100vw !important; //    ( )
  max-width: 100vw !important;
  min-width: 100vw !important;
  background: transparent !important; //   ( )
  margin: 0 !important;
  margin-left: calc(-50vw + 50%) !important; //     
  margin-right: calc(-50vw + 50%) !important; //     
  margin-bottom: 0 !important; // fixed  margin-bottom   ( padding-top )
  padding: 0 !important;
  border-bottom: none !important; //   
  position: fixed !important; //   fixed
  //       ( CSS  )
  top: calc(var(--header-height, 90px) + 20px) !important; //   + (20px) 
  left: 0 !important; //   ( )
  right: 0 !important; //  ( )
  z-index: 99 !important; //     
  display: flex;
  justify-content: center; //  
  box-sizing: border-box !important;
  transform: none !important;
  
  //    ( )
  &__container {
    display: flex;
    width: 319px; //  
    height: 60px; //  
    background: #F8EDDA; //   
    padding: 10px; //      
    gap: 10px; //    
    align-items: center;
    justify-content: center;
    border-radius: $border-radius-md; //    (6px)
  }
  
  &__button {
    width: 145px; //  
    height: 40px; //  
    padding: 0;
    background: transparent; // :  
    color: #A16A51; // :   (#A16A51)
    border: none;
    border-radius: $border-radius-sm; //    (4px)
    font-size: 14px;
    font-weight: 500;
    font-family: $font-family-toggle !important; //     
    cursor: pointer;
    transition: all 0.2s ease;
    text-align: center;
    display: flex;
    align-items: center;
    justify-content: center;
    position: relative;
    flex-shrink: 0; //  
    
    //  
    &--active {
      background: #A16A51 !important; // :   (#A16A51)
      color: $white !important; //  
      font-weight: 600;
    }
    
    &:hover:not(&--active) {
      background: rgba(#A16A51, 0.1); //     
    }
    
    &:active {
      transform: scale(0.98);
    }
  }
  
  //  
  @media (max-width: 767px) {
    top: calc(var(--header-height, 52px) + 20px) !important; //    + (20px) 
    
    &__container {
      width: calc(100% - 32px); //    
      max-width: 319px;
      padding: 8px;
      gap: 8px;
    }
    
    &__button {
      width: calc(50% - 4px); //  
      min-width: 145px;
      font-size: 13px;
    }
  }
}

