/**
 * AppointmentLayout 
 *    
 * : () > () >  ()
 */
@use '../../styles/variables' as *;

.appointment-layout {
  width: 100%;
  position: relative;
  background: $background-appointment;
  min-height: 100vh;
  
  //  fixed ()
  // : position: fixed, top: 0, height: 90px, z-index: 100
  
  //   -     
  &__body {
    position: fixed;
    top: 90px; //  
    left: 0;
    right: 0;
    bottom: 60px; //   
    overflow-y: auto;
    overflow-x: hidden;
    -webkit-overflow-scrolling: touch;
    background: $background-appointment;
    padding-bottom: $spacing-lg; //  
    will-change: scroll-position;
    overscroll-behavior-y: contain;
  }
  
  //  
  &__content {
    padding: $spacing-sm $spacing-xs; //   (  )
    max-width: 100%;
    margin: 0 auto;
    box-sizing: border-box;
    width: 100%;
  }
  
  //    ()
  &__footer {
    position: fixed;
    bottom: 0;
    left: 0;
    right: 0;
    width: 100vw;
    max-width: 100vw;
    min-width: 100vw;
    box-sizing: border-box;
    z-index: 1000; //  
    padding: 0;
    margin: 0;
    margin-left: calc(-50vw + 50%);
    margin-right: calc(-50vw + 50%);
    transform: none;
    background: transparent;
  }
  
  //   
  &__button {
    width: 100%;
    min-height: 60px;
    background-color: $brand-brown-darker;
    color: $white;
    border: none;
    border-radius: 0; //  
    font-size: 17px;
    font-weight: $font-weight-semibold;
    font-family: var(--font-family-greeting, 'Noto Sans KR', sans-serif);
    padding: 0.75rem 1rem;
    cursor: pointer;
    transition: all 0.3s ease;
    box-shadow: 0 4px 12px rgba($brand-brown-darker, 0.3);
    box-sizing: border-box;
    
    &:hover:not(:disabled) {
      background-color: darken($brand-brown-darker, 5%);
      transform: translateY(-1px);
    }
    
    &:active:not(:disabled) {
      transform: translateY(0);
    }
    
    &:disabled {
      background-color: $gray-400;
      color: $gray-600;
      cursor: not-allowed;
      opacity: 0.6;
    }
  }
  
  //  
  @media (max-width: 767px) {
    &__body {
      top: 52px; //    (default )
      bottom: 60px;
    }
    
    &__content {
      padding: $spacing-md;
    }
    
    &__button {
      min-height: 56px;
      font-size: 16px;
    }
  }
}

