/* ===    === */
:root {
  /*   -    */
  --font-family-greeting: 'Noto Sans KR', -apple-system, BlinkMacSystemFont, sans-serif;
  
  /*    */
  --font-family-item-header: 'Noto Sans KR', -apple-system, BlinkMacSystemFont, sans-serif;
  
  /*    -   */
  /*   : /src/assets/fonts/soraime/ */
  --font-family-item-description: '', 'Noto Sans KR', -apple-system, BlinkMacSystemFont, sans-serif;
  
  /*   (  ) */
  --font-family-primary: 'Noto Sans KR', -apple-system, BlinkMacSystemFont, system-ui, Roboto, sans-serif;
  --font-family-mono: 'JetBrains Mono', 'Fira Code', Consolas, monospace;
}

/* ===    === */
/* 
 *  : --font-family-greeting (Noto Sans KR)
 *  : --font-family-item-header (Noto Sans KR)
 *  : --font-family-item-description ()
 */

