/**
 * BarChart  -    
 */

.welno-bar-chart {
  width: 100%;
  height: 100%;
  position: relative;

  &__svg {
    width: 100%;
    height: 100%;
    overflow: visible !important;
    //    
    overflow-y: visible !important;
    overflow-x: visible !important;
  }

  &__empty {
    display: flex;
    align-items: center;
    justify-content: center;
    height: 100%;
    color: var(--text-secondary);
    font-size: var(--font-size-sm);
  }

  //  
  &__grid-line {
    stroke: #E0E0E0;
    stroke-width: 0.5;
    stroke-dasharray: 2, 4;
    opacity: 0.4;
  }

  // 
  &__reference-lines {
    .welno-bar-chart__reference-line {
      stroke: #B0B0B0; //    (  )
      stroke-width: 0.3; //   
      stroke-dasharray: 1, 2; //    
      opacity: 0.8; //  

      &--normal {
        stroke: var(--color-success);
        stroke-width: 1;
        opacity: 0.7;
      }

      &--min {
        stroke: var(--color-warning);
        stroke-width: 1;
        opacity: 0.7;
      }

      &--max {
        stroke: var(--color-danger);
        stroke-width: 1;
        opacity: 0.7;
      }
    }
  }

  //  
  &__group {
    .welno-bar-chart__bar {
      transition: all 0.2s ease;
      cursor: pointer;
      stroke: #8b7d6b !important; //   
      stroke-width: 1;
      fill: #7c746a !important; //     
      
      //   
      &, &[fill], &[style] {
        fill: #7c746a !important;
        stroke: #8b7d6b !important;
      }

      &:hover {
        opacity: 0.8;
        stroke-width: 2;
      }

      //    
      &--normal {
        fill: var(--color-success) !important;
      }

      &--warning {
        fill: var(--color-warning) !important;
      }

      &--danger {
        fill: var(--color-danger) !important;
      }
    }

    .welno-bar-chart__value-label {
      fill: #626262 !important; //    (   )
      font-size: 12px !important;
      font-family: 'Pretendard', -apple-system, BlinkMacSystemFont, system-ui, sans-serif !important;
      font-weight: bold !important;
      pointer-events: none;
      opacity: 1 !important;
      visibility: visible !important;
    }
  }

  // 
  &__x-axis,
  &__y-axis {
    .welno-bar-chart__axis-line {
      stroke: var(--border-medium);
      stroke-width: 1;
    }

    .welno-bar-chart__axis-label {
      fill: var(--text-secondary);
      font-size: 12px;
      font-family: var(--font-family-primary);
    }

    .welno-bar-chart__axis-title {
      fill: var(--text-primary);
      font-size: var(--font-size-sm);
      font-weight: var(--font-weight-medium);
      font-family: var(--font-family-primary);
    }
  }
}

//   
.welno-chart-tooltip {
  &__title {
    font-weight: var(--font-weight-semibold);
    margin-bottom: 4px;
  }

  &__label {
    font-size: var(--font-size-xs);
    opacity: 0.9;
    margin-bottom: 2px;
  }

  &__value {
    font-weight: var(--font-weight-medium);
    margin-bottom: 4px;
  }

  &__date {
    font-size: var(--font-size-xs);
    opacity: 0.8;
    margin-bottom: 2px;
  }

  &__location {
    font-size: var(--font-size-xs);
    opacity: 0.8;
    margin-bottom: 2px;
  }

  &__status {
    font-size: var(--font-size-xs);
    padding: 2px 6px;
    border-radius: var(--border-radius-sm);
    
    &.status-normal {
      background-color: var(--color-success);
      color: var(--text-inverse);
    }

    &.status-warning {
      background-color: var(--color-warning);
      color: var(--text-inverse);
    }

    &.status-danger {
      background-color: var(--color-danger);
      color: var(--text-inverse);
    }
  }
}

//  
@media (max-width: 768px) {
  .welno-bar-chart {
    &__x-axis,
    &__y-axis {
      .welno-bar-chart__axis-label {
        font-size: 12px;
      }
    }

    &__group {
      .welno-bar-chart__value-label {
        font-size: 10px;
      }
    }
  }
}

@media (max-width: 480px) {
  .welno-bar-chart {
    &__group {
      .welno-bar-chart__value-label {
        display: none; //    
      }
    }
  }
}

//  
@media (prefers-reduced-motion: reduce) {
  .welno-bar-chart {
    &__group {
      .welno-bar-chart__bar {
        transition: none;
      }
    }
  }
}

//   
@media (prefers-contrast: high) {
  .welno-bar-chart {
    &__group {
      .welno-bar-chart__bar {
        stroke-width: 2;
      }
    }

    &__reference-lines {
      .welno-bar-chart__reference-line {
        stroke-width: 3;
      }
    }
  }
}
