/**
 * AppointmentPage 
 */
@use '../../styles/variables' as *;

.appointment-page {
  width: 100%;
  
  //  
  &__instruction {
    font-size: $font-base; //  : 16px
    color: $brand-brown-darker; // #55433B ( )
    font-weight: $font-weight-normal;
    line-height: 1.4; //  
    margin-bottom: $spacing-sm; //  
    text-align: center;
    font-family: var(--font-family-greeting, 'Noto Sans KR', sans-serif);
    padding: 0 $spacing-xs; //   
  }
  
  //  
  &__calendar {
    margin-bottom: $spacing-sm; //  
    
    .calendar-placeholder {
      background: $white;
      border: 1px solid $border-beige;
      border-radius: $border-radius-lg;
      padding: $spacing-xl;
      text-align: center;
      color: $gray-600;
      font-size: $font-sm;
    }
  }
  
  //  
  &__legend {
    margin-bottom: $spacing-xs; //  
    
    .legend-placeholder {
      text-align: center;
      color: $gray-600;
      font-size: $font-sm;
    }
  }
  
  //  
  &__notice {
    font-size: $font-xs; //  : 12px
    color: $black;
    line-height: 1.4; //  
    text-align: center;
    margin-bottom: $spacing-xs; //  
    font-family: var(--font-family-greeting, 'Noto Sans KR', sans-serif);
    padding: 0 $spacing-xs; //   
  }
}

