/**
 * ContentLayoutWithHeader 
 *     
 * : () > () > () >  (,  )
 */
@use '../../styles/variables' as *;

.content-layout-with-header {
  width: 100%;
  position: relative;
  background: #F5F5F5;
  //     (  fixed)
  
  //  fixed  ( )
  // : position: fixed, top: 0, height: 90px, z-index: 100
  
  //  fixed  ( )
  // : position: fixed, top: 100px ( 90px +  10px), z-index: 99
  
  //   -     
  &__body {
    position: fixed;
    // : top: calc(var(--header-height, 90px) + 10px),   height: 60px
    //  : calc(var(--header-height, 90px) + 10px) + 60px
    //   : 24px ( padding-bottom)
    top: calc(var(--header-height, 90px) + 10px + 60px + 24px); //   + (10px) + (60px) + (24px)
    left: 0;
    right: 0;
    bottom: 0;
    overflow-y: auto; //    
    overflow-x: hidden;
    -webkit-overflow-scrolling: touch;
    background: #F5F5F5;
    padding-bottom: 80px; //    
    will-change: scroll-position;
    overscroll-behavior-y: contain;
  }
  
  //   - fixed    
  &__toggle {
    //  fixed  div   
    //   fixed   
    position: relative;
    z-index: 99;
    height: 0; //    
    overflow: hidden;
  }
  
  //    -     
  &:not(:has(.health-trends-toggle)) {
    .content-layout-with-header__body {
      top: var(--header-height, 90px); //   (default )
    }
  }
  
  //  
  @media (max-width: 767px) {
    &__body {
      // :   + (10px) + (60px) + (24px)
      top: calc(var(--header-height, 52px) + 10px + 60px + 24px); //    (default )
    }
    
    //    - 
    &:not(:has(.health-trends-toggle)) {
      .content-layout-with-header__body {
        top: var(--header-height, 52px); //    (default )
      }
    }
  }
  
  //   ()
  &__content {
    padding: 0;
    margin-top: 0; //    padding-bottom 
    position: relative;
    background: #F5F5F5;
    // min-height :      
    
    //     /  
    > * {
      margin-top: 0 !important;
      padding-top: 0 !important;
    }
  }
  
  //   
  &:not(:has(.health-trends-toggle)) {
    .content-layout-with-header__content {
      margin-top: 0; //      
    }
  }
  
  // Pull-to-refresh  
  .pull-to-refresh-indicator {
    position: absolute;
    top: 0;
    left: 50%;
    transform: translateX(-50%);
    z-index: 10;
    display: flex;
    flex-direction: column;
    align-items: center;
    gap: 4px;
    
    .refresh-icon {
      font-size: 24px;
      margin-bottom: 4px;
    }
    
    .refresh-text {
      font-size: 14px;
      color: $gray-600;
      font-weight: 500;
      white-space: nowrap;
    }
  }
}

