@use '../styles/variables' as *;
@use '../styles/mixins' as *;

//      ( )
.main-page {
  width: 100%;
  min-height: 100vh;
  background: $background-cream !important; //   (  )
  padding: 0 !important; //   
  margin: 0 !important; //   
  
  //  +   ( )
  &__header-greeting-section {
    background: #FEF9EE !important; //    
    padding: 0 1rem $spacing-lg 1rem !important; //    (1rem = 16px)
    padding-top: 0 !important; //    0
    margin: 0 !important; //   
    margin-top: 0 !important; //    0
    border-radius: 0 0 $border-radius-xl $border-radius-xl; //  
    border-bottom: none !important; //  
    box-shadow: $shadow-subtle !important; //   
    position: relative !important; // z-index   position 
    z-index: 10 !important; //     z-index 
    overflow: hidden !important; //   border-radius  
    //   
    width: 100%;
    box-sizing: border-box;
  }
  
  //  ( + )
  &__header {
    padding: 0 !important;
    padding-top: 10px !important; //    ( )
    margin: 0 !important;
    background: transparent;
    
    &-logo {
      display: block !important;
      margin: 0 !important;
      padding: 0 !important;
      
      &-image {
        width: auto;
        height: auto;
        max-width: 108px;
        max-height: 108px;
        object-fit: contain;
        flex-shrink: 0;
        border-radius: $border-radius-md;
        margin: 0 !important; //    
        margin-bottom: 0 !important; //    
        padding: 0 !important; //    
        display: block !important; //   
        // vertical-align display: block   
        line-height: 0 !important; //   0 
      }
      
      &-icon {
        display: none !important; //     ()
      }
    }
  }
  
  &__greeting {
    padding: 0 0 $spacing-md 0 !important;
    padding-top: 8px !important; //     
    padding-left: 0 !important;
    margin: 0 !important;
    margin-left: 0 !important;
    margin-bottom: 0 !important;
    text-align: left !important; //   
    background: transparent;
    position: relative;
    z-index: 1;
    width: 100%;
    font-family: var(--font-family-greeting) !important; //    Noto Sans KR
    line-height: 1.2 !important; //   
    
    &-title {
      font-size: 22px !important; //      22px
      font-weight: $font-weight-normal !important; // ""  
      color: $black !important; // 
      margin: 0 !important; //   
      margin-left: 0 !important; //  
      padding: 0 !important; //   
      padding-left: 0 !important; //   
      line-height: 1.2 !important; // line-height 
      display: block;
      text-align: left !important; //   
      font-family: var(--font-family-greeting) !important; // Noto Sans KR
      
      .patient-name {
        color: $black !important; // 
        font-weight: $font-weight-bold !important; //   
        font-family: var(--font-family-greeting) !important; // Noto Sans KR
      }
      
      .greeting-text {
        color: $black !important; // 
        font-weight: 300 !important; // 
        font-family: var(--font-family-greeting) !important; // Noto Sans KR
      }
    }
    
    &-subtitle {
      font-size: 22px !important; // title   (22px)
      font-weight: $font-weight-normal !important; //  
      color: #000000 !important; //  
      margin: 0 0 $spacing-md 0 !important; //   
      margin-left: 0 !important; //  
      padding: 0 !important; //   
      padding-left: 0 !important; //   
      display: block;
      line-height: 1.2 !important; // line-height 
      text-align: left !important; //   
      font-family: var(--font-family-greeting) !important; // Noto Sans KR
      
      .hospital-name {
        color: $brand-brown-dark !important; //   
        font-family: var(--font-family-greeting) !important; // Noto Sans KR
      }
      
      .hospital-suffix {
        color: #000000 !important; // "" 
        font-weight: 300 !important; // 
        font-family: var(--font-family-greeting) !important; // Noto Sans KR
      }
    }
    
    &-message {
      font-size: 14px !important; //    14px
      color: $gray-450 !important; //  
      line-height: 1.2 !important; //   
      margin: 0 !important; //   
      margin-left: 0 !important; //   
      padding: 0 !important; //   
      padding-left: 0 !important; //   
      display: block;
      white-space: pre-line !important; //  
      text-align: left !important; //   
      font-family: var(--font-family-greeting) !important; // Noto Sans KR
      
      .hospital-name {
        color: $brand-brown-dark !important; //   
        font-family: var(--font-family-greeting) !important; // Noto Sans KR
      }
      
      .greeting-text {
        color: #565656 !important; //  
        font-weight: $font-weight-normal !important; //  
        font-family: var(--font-family-greeting) !important; // Noto Sans KR
      }
      
      .greeting-text-thin {
        color: #565656 !important; //  
        font-weight: 300 !important; //  (light weight)
        font-family: var(--font-family-greeting) !important; // Noto Sans KR
      }
      
      // br   
      br {
        line-height: 1.2 !important;
        margin: 0 !important;
        padding: 0 !important;
      }
    }
  }
  
  //     (   )
  &__primary-card-wrapper {
    padding: $spacing-md 0 0 0;
    background: transparent;
  }
  
  //    (   )
  &__secondary-cards-section {
    padding-top: 12px !important; //   12px
    padding-left: 1rem !important; //   1rem (16px)
    padding-right: 1rem !important; //   1rem (16px)
    padding-bottom: 5.5rem !important; //    (   : 64px -> 5.5rem = 88px)
    background: $background-appointment !important; //     (#FFFCF6 -  )
    margin-top: 0 !important; //    
    margin-bottom: 0 !important; //   
    border-radius: $border-radius-xl $border-radius-xl 0 0; //  
    position: relative !important; // z-index   position 
    z-index: 1 !important; //    z-index
  }
  
  &__cards {
    padding: 0 !important;
    padding-top: 0 !important;
    padding-bottom: 0 !important;
    margin: 0 !important;
    margin-top: 0 !important;
    margin-bottom: 0 !important;
    display: flex !important;
    flex-direction: column !important;
    gap: 8px !important; //   8px
    background: transparent !important; //   
    
    .card {
      padding: 6px 16px 4px 16px !important;
      min-height: 60px !important;
      height: auto !important;

      //     ()
      .card__image-area {
        width: 74px !important;
        height: 74px !important;
        display: flex !important;
        align-items: center !important;
        justify-content: center !important;
        position: relative; //   absolute   

        //   (     )
        img[alt*=""], 
        img[alt*=""], 
        img[alt*=""] {
          position: absolute; 
          width: 95px !important; // 10%  (105px -> 95px)
          height: 95px !important;
          max-width: none !important;
          object-fit: contain !important;
          right: -2px; //       
        }

        //   (   -   )
        img[alt*=""] {
          width: 65% !important;
          height: 65% !important;
          object-fit: contain !important;
        }
      }

      //    -1
      .card__title {
        font-size: 15px !important;
      }
      
      //    
      .card__main-content {
        min-height: 74px !important;
      }
    }
  }
  
  //      ( ,  )
  &__card {
    background: $overlay-strong;
    backdrop-filter: blur(16px);
    padding: $spacing-sm $spacing-md; //   
    border-radius: $border-radius-xl;
    box-shadow: $shadow-md;
    border: 1px solid $gray-100;
    display: flex;
    align-items: center; //    (, ,   )
    gap: $spacing-md;
    transition: box-shadow 0.3s ease;
    cursor: pointer;
    min-height: 60px !important; //   (80px -> 60px)
    
    &:hover {
      box-shadow: $shadow-lg;
    }
    
    &--primary {
      background: $brand-brown-card !important; //      (  )
      min-height: 60px !important; //  
      border: none !important; //  
    }
    
    //   ( +  + )
    &-main-row {
      display: flex;
      align-items: center;
      gap: $spacing-md;
      width: 100%;
      flex: 1;
    }
    
    &-icon {
      width: 40px !important; //     (48px -> 40px)
      height: 40px !important;
      border-radius: 50% !important; //   
      background-color: $brand-brown-dark !important; //  
      color: white !important;
      display: flex;
      align-items: center !important; //   
      justify-content: center !important; //   
      flex-shrink: 0;
      
      &--brown {
        background-color: $brand-brown-dark !important; //  
      }
      
      &-svg {
        width: 20px !important; // SVG   (24px -> 20px)
        height: 20px !important;
        color: white !important; //   
        margin: 0 !important; //    
      }
    }
    
    &-content {
      flex: 1;
      display: flex;
      flex-direction: column;
      gap: 0 !important; //    
    }
    
    &-title {
      font-size: 14px !important; //   -2 (16px -> 14px)
      font-weight: $font-weight-bold !important; // 
      color: $brand-brown-dark !important; //     
      margin: 0 !important; //   
      margin-bottom: 0 !important; //   
      padding: 0 !important; //   
      line-height: 1.2 !important;
      font-family: var(--font-family-greeting) !important; //    (Noto Sans KR)
      
      &--brown {
        color: #A16A51 !important;
        font-family: var(--font-family-greeting) !important; //   
      }
    }
    
    &-description {
      font-size: 12px !important; //     ($font-sm )
      color: $brand-brown-text !important; //  
      line-height: 1.2 !important; //   
      margin: 0 !important; //   
      margin-top: 0 !important; //   
      padding: 0 !important; //   
      font-weight: 300 !important; // 
      font-family: var(--font-family-greeting) !important; //   
    }
    
    &-arrow {
      width: auto !important; //   
      height: auto !important;
      border-radius: 0 !important; //  
      background-color: transparent !important; //  
      color: #A16A51 !important; //    (#A16A51)
      display: flex;
      align-items: center;
      justify-content: center;
      flex-shrink: 0;
      margin-left: $spacing-sm; //   
      
      &-svg {
        width: 20px;
        height: 20px;
        color: #A16A51 !important; //   (#A16A51)
      }
    }
    
    //   ( ) -   
    &-arrow-bottom {
      width: 27px !important; // 15%  (32px * 0.85 = 27.2px)
      height: 27px !important;
      border-radius: 6px !important; //    
      background-color: $white !important; //  
      color: #A16A51 !important; //  
      display: flex;
      align-items: center;
      justify-content: center;
      flex-shrink: 0;
      margin-left: auto; //  
      
      .main-page__card-arrow-svg {
        width: 16px !important; //   
        height: 16px !important;
        color: #A16A51 !important; //  
      }
    }
  }
  
}

//   (  )
.main-page-loading {
  display: flex;
  align-items: center;
  justify-content: center;
  min-height: 60vh;
  padding: $spacing-xl;
  
  .loading-spinner {
    text-align: center;
    
    .spinner {
      width: 48px;
      height: 48px;
      border: 4px solid rgba($brand-brown, 0.2);
      border-top-color: $brand-brown;
      border-radius: 50%;
      animation: spin 1s linear infinite;
      margin: 0 auto $spacing-md;
    }
    
    p {
      font-size: $font-base;
      color: $gray-600;
      margin: 0;
    }
  }
}

@keyframes spin {
  to {
    transform: rotate(360deg);
  }
}

//  
@include respond-to(md) {
  .main-page {
    //      (  )
    &__header-greeting-section {
      //    
      padding: 0 $spacing-lg $spacing-xl !important;
      padding-top: 0 !important; //    0
      margin-top: 0 !important; //    0
    }
    
    &__greeting {
      //     (   )
      padding: 0 0 $spacing-md 0 !important;
      
      &-title {
        font-size: 24px !important; //  22px  
      }
      
      &-subtitle {
        font-size: 24px !important; //  22px  
      }
      
      &-message {
        font-size: 15px !important; //  14px  
      }
    }
    
    &__cards {
      padding: 0 !important;
      padding-top: 0 !important;
      padding-bottom: 0 !important;
      gap: 8px !important; //   8px
      max-width: 100%; //   
      margin: 0 auto;
    }
    
    &__footer {
      padding: $spacing-xl $spacing-lg;
    }
  }
}

