/**
 * IntroTeaser 
 */
 @use '../../../styles/variables' as *;
 @use '../../../styles/mixins' as *;
 
 .intro-teaser {
   position: fixed;
   top: 0;
   left: 0;
   right: 0;
   bottom: 0;
   z-index: 10000;
   display: flex;
   align-items: center;
   justify-content: center;
 
   //  
   &__overlay {
     position: absolute;
     top: 0;
     left: 0;
     right: 0;
     bottom: 0;
     background: rgba(0, 0, 0, 0.75);
     backdrop-filter: blur(4px);
     cursor: pointer;
   }
 
   //   ( )
   &__modal {
     position: relative;
     width: 90%;
     max-width: 420px; //   
     height: 65vh; //   ()
     max-height: 600px;
     background: rgba(20, 20, 20, 0.95); //    
     border-radius: $border-radius-xl;
     box-shadow: 0 20px 60px rgba(0, 0, 0, 0.5);
     overflow: hidden; //    
     display: flex;
     flex-direction: column;
     z-index: 10001;
     animation: introTeaserFadeIn 0.3s ease-out;
     border: 1px solid rgba(255, 255, 255, 0.1);
   }
 
   //  
   &__close-btn {
     position: absolute;
     top: 16px;
     right: 16px;
     width: 32px;
     height: 32px;
     border: none;
     background: rgba(0, 0, 0, 0.3);
     border-radius: 50%;
     font-size: 20px;
     color: rgba(255, 255, 255, 0.8);
     cursor: pointer;
     z-index: 10002;
     display: flex;
     align-items: center;
     justify-content: center;
     transition: all 0.2s ease;
     backdrop-filter: blur(4px);
 
     &:hover {
       background: rgba(255, 255, 255, 0.2);
       color: #fff;
     }
   }
 
  //   
  &__slide-container {
    flex: 1; //    
    width: 100%;
    height: 100%;
    overflow: hidden;
    position: relative;
    cursor: grab;
    user-select: none; //     

    &:active {
      cursor: grabbing;
    }

    &--dragging {
      cursor: grabbing;
    }
  }
 
   &__slide-wrapper {
     display: flex;
     width: 100%;
     height: 100%;
     transition: transform 0.3s ease-in-out;
   }
 
   //   (Flex Column )
   &__slide {
     min-width: 100%;
     width: 100%;
     height: 100%;
     display: flex;
     flex-direction: column; //  
     box-sizing: border-box;
     
     // []    (   )
     padding-top: 24px; 
     padding-left: 20px;
     padding-right: 20px;
   }
 
   // 1.   ( ,  )
   &__image-container {
     position: relative;
     width: 100%;
     flex: 1; //    /   
     min-height: 0; // Flex item  
     border-radius: $border-radius-lg; //    
     overflow: hidden; // []       
     background: rgba(255, 255, 255, 0.05); //   
     margin-bottom: $spacing-md; //   
 
     //   (  )
     &::after {
       content: '';
       position: absolute;
       top: 0; left: 0; right: 0; bottom: 0;
       z-index: 2;
       pointer-events: none;
       //        
       background: linear-gradient(to bottom, 
         rgba(0,0,0,0) 0%, 
         rgba(0,0,0,0) 70%, 
         rgba(0,0,0,0.4) 100%
       );
     }
   }
 
  //   ( )
  &__image {
    width: 100%;
    height: 130%; //        
    object-fit: cover;
    display: block;
    //     ( )
    animation: teaserImagePan 10s ease-in-out infinite alternate;
  }
 
   // 2.  
   &__text-area {
     flex-shrink: 0; //   
     text-align: center;
     padding: 0 $spacing-xs;
     margin-bottom: $spacing-md;
     z-index: 5;
   }
 
   &__title {
     font-size: 20px;
     font-weight: $font-weight-bold;
     color: #fff;
     margin-bottom: 8px;
     line-height: 1.3;
   }
 
  &__description {
    font-size: 14px;
    color: rgba(255, 255, 255, 0.7);
    line-height: 1.5;
    margin: 0;
    
    //   ()
    display: -webkit-box;
    -webkit-line-clamp: 3;
    line-clamp: 3; //   
    -webkit-box-orient: vertical;
    overflow: hidden;
  }
 
   // 3.  
   &__indicators {
     flex-shrink: 0;
     display: flex;
     justify-content: center;
     gap: 8px;
     margin-bottom: $spacing-lg;
     padding: 0;
   }
 
   &__indicator {
     width: 6px;
     height: 6px;
     border-radius: 50%;
     background: rgba(255, 255, 255, 0.2);
     border: none;
     padding: 0;
     cursor: pointer;
     transition: all 0.3s cubic-bezier(0.25, 0.8, 0.25, 1);
 
     &--active {
       background: $brand-brown; // 
       width: 18px;
       border-radius: 10px;
     }
   }
 
   // 4.  ( )
   &__actions {
     flex-shrink: 0; //  
     width: 100%;
     display: flex;
     gap: 12px;
     padding: 20px; //  
     padding-top: 16px;
     border-top: 1px solid rgba(255, 255, 255, 0.1); // 
     background: rgba(0, 0, 0, 0.2); //    
     margin-top: auto; //     
   }
 
   &__action-btn {
     flex: 1;
     height: 48px; //   
     border: none;
     border-radius: $border-radius-md;
     font-size: 14px;
     font-weight: 600;
     cursor: pointer;
     transition: transform 0.1s ease, background 0.2s;
     display: flex;
     align-items: center;
     justify-content: center;
 
     &:active {
       transform: scale(0.98);
     }
 
     // "  " 
     &--dont-show {
       background: rgba(255, 255, 255, 0.1);
       color: rgba(255, 255, 255, 0.8);
 
       &:hover {
         background: rgba(255, 255, 255, 0.15);
         color: #fff;
       }
     }
 
     // "" 
     &--close {
       background: $brand-brown;
       color: #fff;
 
       &:hover {
         background: lighten($brand-brown, 5%);
       }
     }
   }
 }
 
 //  
 @keyframes introTeaserFadeIn {
   from {
     opacity: 0;
     transform: scale(0.95) translateY(10px);
   }
   to {
     opacity: 1;
     transform: scale(1) translateY(0);
   }
 }
 
@keyframes teaserImagePan {
  0% {
    transform: translateY(0); //  
  }
  100% {
    transform: translateY(-20%); //       
  }
}
 
 //   
 @media (max-width: 768px) {
   .intro-teaser {
     &__modal {
       width: 92%; //    
       height: 70vh; //    
     }
     
     &__slide {
       padding-top: 20px; //    
       padding-left: 16px;
       padding-right: 16px;
     }
 
     &__image-container {
       margin-bottom: 16px;
     }
     
     &__title {
       font-size: 18px;
     }
     
     &__description {
       font-size: 13px;
     }
 
     &__actions {
       padding: 16px;
     }
     
     &__action-btn {
       height: 44px;
     }
   }
 }