/**
 * Legend 
 *  
 */
@use '../../../styles/variables' as *;

.legend {
  display: flex;
  align-items: center;
  justify-content: center;
  gap: $spacing-md; //  
  margin: $spacing-xs 0; //  
  
  &__item {
    display: flex;
    align-items: center;
    gap: $spacing-sm;
  }
  
  &__circle {
    width: 16px;
    height: 16px;
    border-radius: 50%;
    flex-shrink: 0;
    
    //   
    &--today {
      background: $brand-brown-light-bg;
    }
    
    //   
    &--selected {
      background: $brand-brown-dark;
    }
  }
  
  &__text {
    font-size: $font-base; //    ( : 16px)
    color: $black;
    font-weight: $font-weight-normal;
    font-family: var(--font-family-greeting, 'Noto Sans KR', sans-serif);
  }
  
  //  
  @media (max-width: 767px) {
    gap: $spacing-sm; //  
    
    &__circle {
      width: 16px;
      height: 16px;
    }
    
    &__text {
      font-size: $font-sm; //    ( : 14px)
    }
  }
}

