/**
 *       ( )
 */
@use '../../../styles/variables' as *;
@use '../../../styles/mixins' as *;

.health-trends-refresh-modal {
  padding: $spacing-md $spacing-xs $spacing-sm $spacing-xs; //    (, , , )
  text-align: center;

  &__title {
    font-size: $font-lg;
    font-weight: $font-weight-semibold;
    color: $gray-800;
    margin: 0 0 $spacing-sm 0;
    font-family: $font-family-base;
  }

  &__description {
    font-size: $font-sm;
    color: $gray-600;
    margin: 0 0 $spacing-lg 0;
    line-height: 1.5;
    font-family: $font-family-base;
  }

  &__withdraw-section {
    margin: $spacing-md 0;
    padding: $spacing-sm;
    border: 1px solid $gray-200;
    border-radius: $border-radius-md;
    background: $gray-50;
  }

  &__withdraw-checkbox {
    display: flex;
    align-items: center;
    gap: $spacing-xs;
    cursor: pointer;
    user-select: none;
    
    input[type="checkbox"] {
      width: 18px;
      height: 18px;
      cursor: pointer;
      accent-color: $brand-brown;
    }
  }

  &__withdraw-label {
    font-size: $font-sm;
    color: $gray-700;
    font-weight: $font-weight-medium;
  }

  &__withdraw-warning {
    margin-top: $spacing-xs;
    font-size: $font-xs;
    color: $gray-600;
    line-height: 1.4;
    padding-left: 26px; //   + gap
  }

  &__actions {
    display: flex;
    gap: 0; // gap 
    justify-content: stretch; //    
    margin-top: $spacing-md; //    ($spacing-lg  $spacing-md)
    margin-bottom: 0; //   
    width: 100%; //   
  }

  &__btn {
    padding: 10px 16px; //  
    border: none;
    border-radius: $border-radius-xl;
    font-size: $font-sm; //    ($font-base  $font-sm)
    font-weight: $font-weight-medium; //    ($font-weight-semibold  $font-weight-medium)
    cursor: pointer;
    transition: all 0.3s ease;
    font-family: $font-family-base;
    flex: 1; //   
    width: 100%; //   

    &--confirm {
      background: $brand-brown; //   
      color: $white; //   

      &:hover {
        background: $brand-brown-hover; //   
        transform: translateY(-1px);
      }
    }
  }
}


