/**
 * LineChart  -    
 */

.welno-line-chart {
  width: 100%;
  height: 100%;
  position: relative;
  overflow: visible !important; //     

  &__svg {
    width: 100%;
    height: 100%;
    overflow: visible !important; //     
  }

  &__empty {
    display: flex;
    align-items: center;
    justify-content: center;
    height: 100%;
    color: var(--text-secondary);
    font-size: var(--font-size-sm);
  }

  //  
  &__grid-line {
    stroke: var(--border-light);
    stroke-width: 1;
    stroke-dasharray: 2,2;
    opacity: 0.5;
  }

  // 
  &__reference-lines {
    .welno-line-chart__reference-line {
      stroke-width: 1;
      stroke-dasharray: 4,4;
      opacity: 0.7;

      &--optimal {
        stroke: var(--color-success);
      }

      &--min {
        stroke: var(--color-warning);
      }

      &--max {
        stroke: var(--color-danger);
      }
    }
  }

  //  
  &__series {
    .welno-line-chart__area {
      transition: fill-opacity 0.2s ease;
    }

    .welno-line-chart__line {
      transition: stroke-width 0.2s ease;
      
      &:hover {
        stroke-width: 3;
      }
    }

    .welno-line-chart__point {
      //  SVG r  CSS    
      //    SVG   
      transition: stroke-width 0.2s ease, fill-opacity 0.2s ease;
      cursor: pointer;

      &:hover {
        //  r   -   
        stroke-width: 3;
      }

      //    (  ) -    !important 
      &--normal {
        // fill   
      }

      &--warning {
        // fill   
      }

      &--danger,
      &--abnormal {
        // fill   
      }

      &--neutral,
      &--unknown {
        // fill   
      }
      
      //   
      &--selected {
        //    SVG   
      }
    }
  }

  // 
  &__x-axis,
  &__y-axis {
    .welno-line-chart__axis-line {
      stroke: var(--border-medium);
      stroke-width: 1;
    }

    .welno-line-chart__axis-label {
      fill: var(--text-secondary);
      font-size: 12px;
      font-family: var(--font-family-primary);
    }

    .welno-line-chart__axis-title {
      fill: var(--text-primary);
      font-size: var(--font-size-sm);
      font-weight: var(--font-weight-medium);
      font-family: var(--font-family-primary);
    }
  }
}

//    (   )
.welno-chart-tooltip {
  &__header {
    font-size: 10px;
    font-weight: var(--font-weight-medium);
    margin-bottom: 2px;
    color: #6b5b47;
    opacity: 0.9;
  }

  &__value {
    font-size: 11px;
    font-weight: var(--font-weight-semibold);
    color: #5d4e37;
    margin: 0;
  }
}

//  
@media (max-width: 480px) {
  .welno-line-chart {
    &__x-axis,
    &__y-axis {
      .welno-line-chart__axis-label {
        font-size: 12px;
      }
    }

    &__series {
      .welno-line-chart__point {
        //  SVG r  CSS    
        //    SVG   

        &:hover {
          //  r   -   
        }
      }
    }
  }
}

//  
@media (prefers-reduced-motion: reduce) {
  .welno-line-chart {
    &__series {
      .welno-line-chart__area,
      .welno-line-chart__line,
      .welno-line-chart__point {
        transition: none;
      }
    }
  }
}
